import React from 'react';
import { connect } from 'react-redux';
import Users from './Users';
import { setCurrentPage, getUsers, unfollow, follow } from '../../redux/users-reducer';
import Preloader from '../Common/Preloader/Preloader';
import { compose } from 'redux';
import { getAllUsers, getPageSize, getTotalUsersCount, 
    getCurrentPage, getIsFetching, getFollowingInProgress, getAllUsersSuperSelector, getPortionSize  } from '../../redux/users-selectors';

class UsersContainer extends React.Component {

    componentDidMount() {
        const {pageSize, currentPage} = this.props;
        this.props.getUsers(pageSize, currentPage);
    }

    onPageChanged = (pageNumber) => {
        const {pageSize} = this.props;
        this.props.getUsers(pageSize, pageNumber);
    }
    
    render() {
        return <>
            {this.props.isFetching ? <Preloader /> : null}
            <Users
                totalItemsCount={this.props.totalItemsCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                users={this.props.users}
                onPageChanged={this.onPageChanged}
                unfollow={this.props.unfollow}
                follow={this.props.follow}
                followingInProgress={this.props.followingInProgress}
                portionSize={this.props.portionSize}
            />
        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: getAllUsers(state),
        // users: getAllUsersSuperSelector(state),
        pageSize: getPageSize(state),
        totalItemsCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        portionSize: getPortionSize(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state)
    }
}

export default compose(
    connect(mapStateToProps,
        {setCurrentPage, getUsers, unfollow, follow}
    ),
)(UsersContainer);
