import React from 'react';
import classes from './Navbar.module.css';
import { NavLink } from 'react-router-dom';
import Sidebar from '../Sidebar/Sidebar';

// let classes = {
//   'nav': 'Navbar_nav__3LcEq',
//   'item': 'Navbar_item__23SEc '
// }
// `${classes.item} ${classes.active}` - шаблонна строка в ES6

const Navbar = (props) => {
  
  return (
    <div>
      <nav className={classes.nav}>
        <div className={`${classes.item} ${classes.active}`}>
          <NavLink to='/profile' activeClassName={classes.activeLink}>Profile</NavLink>
        </div>
        <div className={classes.item}>
          <NavLink to='/dialogs' activeClassName={classes.activeLink}>Massages</NavLink>
        </div>
        <div className={classes.item}>
          <NavLink to='/news' activeClassName={classes.activeLink}>News</NavLink>
        </div>
        <div className={classes.item}>
          <NavLink to='/music' activeClassName={classes.activeLink}>Music</NavLink>
        </div>
        <div className={`${classes.item} ${classes.users}`}>
          <NavLink to='/users' activeClassName={classes.activeLink}>Find users</NavLink>
        </div>
        <div className={classes.item}>
          <NavLink to='/settings' activeClassName={classes.activeLink}>Settings</NavLink>
        </div>
      </nav>
      <Sidebar/>
    </div>
  )
}

export default Navbar;