const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
  dialogs: [
    {id: 1, name: 'Marta'},
    {id: 2, name: 'Stephan'},
    {id: 3, name: 'Olga'}
  ],
  messages: [
    {id: 1, message: 'Hi! How are you?'},
    {id: 2, message: 'Hello! I\'m reading the book. And you?'},
    {id: 3, message: 'I\'m having dinner!'},
    {id: 4, message: 'It\'s very good. Let\'s go for a walk!'},
    {id: 5, message: 'Let\'s go! 10 minutes, please.'}
  ],
};

const dialogsReducer = (state = initialState, action) => {

  switch (action.type) {
    case SEND_MESSAGE: 
      let newMessageBody = {
        id: 6, 
        message: action.newMessage
      }
      return {
        ...state,
        messages: [...state.messages, newMessageBody],
      };
    default:
      return state;
  }
}

export const sendMessageActionCreator = (newMessage) => ({type: SEND_MESSAGE, newMessage})

export default dialogsReducer;